@Composable
fun ContactSection() {
    Box(
        modifier = Modifier
            .fillMaxWidth()
            .padding(top = 100.px, bottom = 100.px)
            .id("contact"),
        contentAlignment = Alignment.Center
    ) {
        Column(
            modifier = Modifier
                .maxWidth(900.px)
                .padding(40.px)
                .backgroundColor(rgba(255, 255, 255, 0.04))
                .backdropFilter(blur(18.px))
                .borderRadius(24.px)
                .boxShadow(
                    offsetX = 0.px,
                    offsetY = 4.px,
                    blurRadius = 26.px,
                    spreadRadius = 8.px,
                    color = rgba(0, 0, 0, 0.35)
                ),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {

            // Title
            SpanText(
                " Contact Me",
                modifier = Modifier
                    .fontSize(32.px)
                    .fontWeight(FontWeight.Bold)
                    .color(Color.white)
                    .padding(bottom = 16.px)
            )

            // Description
            SpanText(
                "Whether you want to discuss a project, collaborate, or just say hello  Im always open to talking.",
                modifier = Modifier
                    .fontSize(18.px)
                    .lineHeight(28.px)
                    .color(rgb(220, 220, 220))
                    .textAlign(TextAlign.Center)
                    .maxWidth(700.px)
                    .padding(bottom = 36.px)
            )

            // Social icons row
            Row(
                modifier = Modifier.gap(30.px),
                verticalAlignment = Alignment.CenterVertically,
                horizontalArrangement = Arrangement.Center
            ) {
                ContactIcon("Email", "/icons/email.png", "gaurav29k06@gmail.com")
                ContactIcon("GitHub", "/tools/GitHub.svg", "https://github.com/gauravsingh2906")
                ContactIcon("LinkedIn", "/icons/linkedin.png", "https://www.linkedin.com/in/gaurav-singh-913b6728a")
                ContactIcon("PlayStore", "/icons/playstore.png", "https://play.google.com/store/apps/details?id=com.futurion.apps.mindmingle")
            }
        }
    }
}


@Composable
fun ContactIcon(name: String, iconPath: String, link: String) {
    A(href = link, attrs = {
        attr("target", "_blank")
    }) {
        Box(
            modifier = Modifier
                .size(48.px)
                .backgroundColor(rgba(255, 255, 255, 0.08))
                .borderRadius(50.percent)
                .padding(10.px)
                .cursor(Cursor.Pointer)
                .transition(Transition.of("transform", 0.3.s))
            ,
            contentAlignment = Alignment.Center
        ) {
            Image(
                src = iconPath,
                description = name,
                modifier = Modifier.size(100.percent)
            )
        }
    }
}
