@OptIn(ExperimentalComposeWebApi::class)
@Composable
fun ProjectCard2(
    imgPath: String,
    title: String,
    desc: String,
    githubUrl: String,
    iconsList: List<String>,
    projectTags: List<String>
) {
    var hovered by remember { mutableStateOf(false) }

    Column(
        modifier = Modifier
            .width(420.px)
            .height(550.px)
            .background(
                com.varabyte.kobweb.compose.ui.graphics.Color
                    .rgba(10, 10, 15, 0.7f)
            )
            .borderRadius(18.px)
            .padding(20.px)
            .styleModifier {
                property(
                    "transition",
                    "transform 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), box-shadow 0.4s ease"
                )
                property(
                    "background",
                    "linear-gradient(145deg, rgba(255,255,255,0.04), rgba(0,0,0,0.25))"
                )
                property("backdrop-filter", "blur(10px)")
                property("border", "1px solid rgba(0,255,255,0.15)")
                property("box-shadow", "0 4px 20px rgba(0,0,0,0.4)")
            }
            .styleModifier {
                if (hovered) {
                    property("transform", "translateY(-10px) scale(1.03)")
                    property("border", "1.5px solid rgba(0,255,255,0.6)")
                    property(
                        "box-shadow",
                        "0 0 25px rgba(0,255,255,0.5), 0 0 50px rgba(0,255,255,0.25)"
                    )
                    property(
                        "animation",
                        "glowPulse 2s ease-in-out infinite"
                    )
                }
            }
//            .boxShadow(
//                offsetX = 0.px,
//                offsetY = if (hovered) 20.px else 8.px,
//                blurRadius = if (hovered) 40.px else 20.px,
//                color = com.varabyte.kobweb.compose.ui.graphics.Color.rgba(
//                    0, 255, 255,
//                    if (hovered) 0.35f else 0.15f
//                )
//            )
            .onMouseEnter { hovered = true }
            .onMouseLeave { hovered = false }
            .onClick { window.open(githubUrl, "_blank") },
        horizontalAlignment = Alignment.CenterHorizontally
    ) {

//        Img(
//            src = imgPath,
//            attrs = Modifier
//                .width(100.percent)
//                .maxHeight(250.px)
//                .objectFit(ObjectFit.Cover)
//                .borderRadius(12.px)
//                .toAttrs()
//        )


        Image(
            src = imgPath,
            description = title,
            modifier = Modifier
                .fillMaxWidth()
                .maxHeight(250.px)
                .borderRadius(14.px)
                .objectFit(ObjectFit.Cover)
                .styleModifier {
                    property("transition", "transform 0.4s ease")
                    if (hovered) {
                        property("transform", "scale(1.05)")
                    }
                }
        )

        SpanText(
            text = title,
            modifier = Modifier
                .fontSize(20.px)
                .fontWeight(FontWeight.Bold)
                .padding(top = 16.px)
                .color(rgb(255, 255, 255))
        )

        SpanText(
            text = desc,
            modifier = Modifier
                .fontSize(14.px)
                .lineHeight(22.px)
                .color(rgba(200, 200, 200, 0.9f))
                .padding(top = 10.px)
                .textAlign(TextAlign.Center)
        )


        Column(
            modifier = Modifier
                .gap(8.px)
                .padding(bottom = 20.px)
                .maxWidth(600.px)
        ) {
            projectTags.forEach { feature ->
                Row(
                    verticalAlignment = Alignment.CenterVertically,
                    modifier = Modifier.gap(10.px)
                ) {
                    SpanText(
                        text = "",
                        modifier = Modifier
                            .fontSize(18.px)
                            .color(rgb(0, 255, 200))
                    )
                    SpanText(
                        text = feature,
                        modifier = Modifier
                            .fontSize(16.px)
                            .color(rgb(220, 220, 220))
                    )
                }
            }
        }


//        Row(
//            modifier = Modifier
//                .fillMaxWidth(95.percent)
//                .padding(top = 15.px), // Space below the description
//            horizontalArrangement = Arrangement.Center
//            // Consider using a FlowRow from a library if available for wrapping tags
//        ) {
//            projectTags.forEach { tag ->
//                FeatureTag(tag)
//            }
//        }

        Div(
            attrs = Modifier.fillMaxHeight().toAttrs() // Takes all remaining space
        ) {}

        Div(
            attrs = Modifier
                .fillMaxWidth(90.percent) // Line width
                .height(1.px)
                .background(rgba(0, 255, 255, 0.4f)) // Subtle cyan line
                .margin(bottom = 10.px) // Space below the line
                .toAttrs()
        ) {}

        SimpleGrid(
            modifier = Modifier
                .fillMaxWidth(90.percent)
                .justifyItems(JustifyItems.Center), // Increase vertical padding for separation
            numColumns = numColumns(base = 4, sm = 5, md = 6) // Allow for more columns if needed
        ) {
            iconsList.forEach { icon ->
                Image(
                    src = icon,
                    modifier = Modifier
                        .size(26.px) // Slightly smaller icons can help with fit
                        .margin(4.px) // Add margin to separate icons
                        .mixBlendMode(MixBlendMode.Normal)
                )
            }
        }

//        SpanText(
//            text = "View on GitHub ",
//            modifier = Modifier
//                .fontSize(14.px)
//                .color(com.varabyte.kobweb.compose.ui.graphics.Color.rgb(0, 255, 255))
//                .fontWeight(FontWeight.Medium)
//                .styleModifier { property("text-decoration", "underline") }
//        )
    }
}

@Composable
fun FeatureTag(text: String) {
    SpanText(
        text = text,
        modifier = Modifier
            .margin(right = 6.px, bottom = 6.px) // Spacing between tags
            .padding(leftRight = 8.px, topBottom = 4.px)
            .borderRadius(10.px)
            .backgroundColor(rgba(0, 255, 255, 0.1f)) // Light cyan background
            .color(rgb(0, 255, 255)) // Cyan text
            .fontSize(12.px) // Small font size
            .fontWeight(FontWeight.Medium)
    )
}
