@Composable
fun SkillBadge(skill: String) {
    Box(
        modifier = Modifier
            .padding(top = 8.px)
            .backgroundColor(rgba(255, 255, 255, 0.08))
            .borderRadius(16.px)
            .padding(topBottom = 10.px, leftRight = 20.px)
            .boxShadow(
                offsetX = 0.px,
                offsetY = 1.px,
                blurRadius = 12.px,
                color = rgba(0, 0, 0, 0.25)
            )
    ) {
        SpanText(
            text = skill,
            modifier = Modifier
                .fontSize(16.px)
                .color(rgb(230, 230, 230))
        )
    }
}
