@Composable
fun Navbar() {
    Row(
        modifier = Modifier
            .fillMaxWidth()
            .padding(20.px)
            .backgroundColor(rgba(0, 0, 0, 0.35))
            .backdropFilter(blur(12.px))
            .borderRadius(18.px)
            .boxShadow(
                blurRadius = 20.px,
                spreadRadius = 2.px,
                color = rgba(0, 255, 255, 0.15)
            )
            .position(Position.Sticky)
            .top(20.px)
            .zIndex(50)
            .justifyContent(JustifyContent.SpaceBetween),
        verticalAlignment = Alignment.CenterVertically
    ) {

        /** LEFT SIDE  NAME **/
        SpanText(
            text = "Gaurav Singh",
            modifier = Modifier
                .fontSize(22.px)
                .fontWeight(FontWeight.Bold)
                .color(Colors.White)
                .letterSpacing(1.px)
                .cursor(Cursor.Pointer)
                .onClick {
                    // Scroll to top smoothly
                    window.scrollTo(js("{ top: 0, behavior: 'smooth' }"))
                }
        )

        /** RIGHT SIDE MENU **/
        Row(
            modifier = Modifier.gap(32.px),
            verticalAlignment = Alignment.CenterVertically
        ) {
            NavItem("About", "about")
            NavItem("Skills", "skills")
            NavItem("Projects", "projects")
            NavItem("Resume", "resume")
            NavItem("Contact", "contact")
        }
    }
}


