@Composable
fun NavItem(text: String, sectionId: String, closeMenu: Boolean = false) {
    SpanText(
        text = text,
        modifier = Modifier
            .fontSize(16.px)
            .color(rgb(255, 255, 255))
            .cursor(Cursor.Pointer)
            .onClick {
                val element = document.getElementById(sectionId)
                element?.scrollIntoView(js("{ behavior: 'smooth' }"))
            }

    )
}
