@Composable
fun ProfileHeader() {
    Box(
        modifier = Modifier
            .fillMaxSize() // takes full viewport height
            .display(DisplayStyle.Flex)
            .justifyContent(JustifyContent.Center) // centers horizontally
            .alignItems(AlignItems.Center) // centers vertically
    ) {
        Box(
            modifier = Modifier
                .fillMaxWidth()
                .maxWidth(1100.px)
                .padding(48.px)
                .margin(top=40.px)
                .backgroundColor(rgba(255, 255, 255, 0.08))
                .backdropFilter(blur(16.px))
                .borderRadius(24.px)
                .boxShadow(
                    offsetX = 0.px,
                    offsetY = 1.px,
                    blurRadius = 20.px,
                    spreadRadius = 10.px,
                    color = rgba(0, 0, 0, 0.25)
                )
        ) {
            Row(
                modifier = Modifier
                    .fillMaxWidth()
                    .gap(48.px)
                    .flexWrap(FlexWrap.Wrap)
                    .justifyContent(JustifyContent.Center),
                verticalAlignment = Alignment.CenterVertically
            ) {
                //  RIGHT SIDE  Profile Picture
                Image(
                    src = "/images/profile2.jpg",
                    description = "Profile Picture",
                    modifier = Modifier
                        .size(250.px)
                        .borderRadius(50.percent)
                        .boxShadow(blurRadius = 12.px, color = rgba(0, 0, 0, 0.2))
                        .objectFit(ObjectFit.Cover)
                )
                // LEFT SIDE  About Section
                Column(
                    modifier = Modifier
                        .flexBasis(50.percent)
                        .maxWidth(500.px)
                ) {
                    SpanText(
                        text = " Hi, I'm",
                        modifier = Modifier
                            .fontSize(18.px)
                            .color(rgb(200, 200, 200))
                            .padding(bottom = 4.px)
                    )
                    SpanText(
                        text = "Gaurav Singh",
                        modifier = Modifier
                            .fontSize(36.px)
                            .fontWeight(FontWeight.Bold)
                            .color(rgb(255, 255, 255))
                            .padding(bottom = 8.px)
                    )
                    SpanText(
                        text = "Android & KMP Developer",
                        modifier = Modifier
                            .fontSize(20.px)
                            .color(rgb(210, 210, 210))
                            .padding(bottom = 8.px)
                    )
                    SpanText(
                        text = "I build cross-platform mobile apps with Kotlin Multiplatform and Jetpack Compose, focusing on clean UI and smooth user experiences. Im also passionate about game development and enjoy creating gameplay systems in Unreal Engine and Godot.",
                        modifier = Modifier
                            .lineHeight(28.px)
                            .fontSize(16.px)
                            .color(rgb(220, 220, 220))
                            .padding(bottom = 8.px)
                    )

                    //  Social Icons Row
                    Row(
                        modifier = Modifier
                            .gap(26.px)
                            .padding(top = 12.px),
                        verticalAlignment = Alignment.CenterVertically
                    ) {
                        SocialIcon("https://github.com/gauravsingh2906") { FaGithub() }
                        SocialIcon("https://www.linkedin.com/in/gaurav-singh-913b6728a") { FaLinkedin() }
                        SocialIcon("https://play.google.com/store/apps/details?id=com.futurion.apps.mindmingle") { FaGooglePlay() }

                    }
                }



            }
        }
    }
}


@Composable
fun SocialIcon(url: String, icon: @Composable () -> Unit) {
    A(
        href = url,
        attrs = {
            attr("target", "_blank")
            attr("rel", "noopener noreferrer")
        }
    ) {
        Box(
            modifier = Modifier
                .fontSize(28.px)
                .color(rgb(255, 255, 255))
                .transition(Transition.of("transform", 0.3.s))
                .cursor(Cursor.Pointer)

        ) {
            icon()
        }
    }
}

@Composable
fun IconLink(icon: Unit, url: String, color: CSSColorValue) {
    Link(
        path = url,
        modifier = Modifier
            .fontSize(28.px)
            .color(color)
            .transition(Transition.of("all", 0.3.s))
    ) {
        FaIcon(name = icon.toString(), modifier = Modifier.cursor(Cursor.Pointer))
    }
}
