@Page
@Composable
fun HomePage() {
    Box(
        modifier = Modifier
            .width(100.vw)
            .height(100.vh)
            .background(Color.transparent)
            .position(Position.Relative)
    ) {
        // Background behind everything
        AnimatedNetworkBackground()

        // Content above
        Column(
            modifier = Modifier
                .zIndex(1)
                .fillMaxWidth()
                .padding(leftRight = 40.px, bottom = 40.px)
        ) {
            Navbar()
            ProfileHeader()
            AboutSection()
            SkillsSection()
          //  LanguagesSection()
          //  CodeSphere3D()
         //   SkillAnimationsSection()
         //   SkillOrbitSection()
            Projects()
            ResumeSection()
            ContactSection()

        }
    }
}







