enum class Theme(val color: CSSColorValue) {
    Gray(color = rgb(r=238,g=238,b=238)),
    Blue(color = rgb(r=28,g=181,b=244))
}

val PageContentStyle = CssStyle {
    base {
        Modifier
            .fillMaxSize()
            .padding(leftRight = 1.cssRem, top = 4.cssRem)
    }
    Breakpoint.MD {
        Modifier.maxWidth(100.cssRem)
    }
}

@OptIn(DelicateApi::class)
@Composable
fun PageLayout(
    title: String,
    content: @Composable ColumnScope.() -> Unit
) {
    val colorMode by ColorMode.currentState
    val breakpoint = rememberBreakpoint()

    LaunchedEffect(title) {
        document.title = "Vikramaditya Khupse - $title"
    }

    Box(
        modifier = Modifier
            .fillMaxSize()
            .cursor(Cursor.None)
//            .styleModifier {
//                property("cursor", "url('${
//                    if (colorMode.isDark) Res.Image.CUSTOM_CURSOR_DARK
//                    else Res.Image.CUSTOM_CURSOR_LIGHT
//                }'), auto")
//            }
    ) {
        //  Matrix Rain Background
       AnimatedNetworkBackground()




        // Overlay Layer (faint black for dark mode, white for light)
        Box(
            modifier = Modifier
                .id("overlay")
                .fillMaxSize()
                .zIndex(1)
                .backgroundColor(
                    if (colorMode.isDark)
                        com.varabyte.kobweb.compose.ui.graphics.Color.rgba(0, 0, 0, 0.5f)
                    else
                        com.varabyte.kobweb.compose.ui.graphics.Color.rgba(255, 255, 255, 0.5f)
                )
        )

        // Fixed Header (above everything)
        Box(
            modifier = Modifier
                .position(Position.Fixed)
                .zIndex(3)
                .fillMaxWidth()
        ) {

        }

        // Main Content Layer
        Column(
            modifier = PageContentStyle.toModifier()
                .fillMaxSize()
                .zIndex(2)
                .padding(top = 80.px)
        ) {
            content()
           // BackToTopButton()
        }
    }
}